import { createStore, StoreApi } from "zustand";
import { db, StoreType } from "../../db";

type State = {
  list: {
    id: string;
    name: string;
  }[];
  type: StoreType;
};

type Actions = {
  fetchList(): Promise<void>;
  setCurrentType(type: StoreType): void;
};

export type ListStore = State & Actions;

export const listStore = createListStore();

export function createListStore(): StoreApi<ListStore> {
  return createStore((set, get) => {
    return {
      list: [],
      type: StoreType.activities,
      setCurrentType(type) {
        set({ type });
      },
      async fetchList() {
        const res = await db[get().type].toArray();
        set({
          list: res.map((item) => ({
            id: item.id,
            name: item.name,
          })),
        });
      },
    };
  });
}
