import { StoreApi } from "zustand";
import { db, StoreType } from "../../../db";
import { DiagramType } from "../../../shared/constants";
import { ListStore } from "../../../shared/store/listStore";

export async function createDiagram(props: {
  diagramId: string;
  type: StoreType;
  listStore: StoreApi<ListStore>;
  newDiagram: {
    id: string;
    name: string;
    diagram: string;
  };
  onNavigate(url: string): void;
  onLoaded(diagram: any): void;
}) {
  props.listStore.getState().setCurrentType(props.type);
  props.listStore.getState().fetchList();

  const diagram = await db[props.type].get(props.diagramId ?? "");
  const diagramList = await db[props.type].toArray();
  const currentDiagram = diagram || diagramList[0];

  if (!currentDiagram) {
    // 
    const result = {
      id: props.newDiagram.id,
      diagram: JSON.stringify(props.newDiagram),
      name: props.newDiagram.name,
    };
    await db.deployments.add(result);
    props.onNavigate(
      `/${
        props.type === StoreType.deployments
          ? DiagramType.deployment
          : DiagramType.activity
      }/${props.newDiagram.id}`
    );
    return;
  }

  if (props.diagramId) {
    props.onLoaded(JSON.parse(currentDiagram.diagram));
    return;
  }
  return props.onNavigate(
    `/${
      props.type === StoreType.deployments
        ? DiagramType.deployment
        : DiagramType.activity
    }/${currentDiagram.id}`
  );
}
